.userNav{
    width: 100%;
    background-color: white;
    display: flex;
    margin: 0;
    justify-content: space-between;
    height: 10vh;
    padding: 5px;
    align-items: center;
   
    
}
.alkalmazott{
    justify-content: center;
}
.logindiv{
    min-width: 100vw;
    min-height: 100vh;
    
}
.formholder{
 display: flex;
 width: 500px;
 background-color: white;
 margin: auto;
 padding: 15px;
 align-items: center;
 flex-direction: column;
 -webkit-box-shadow: -2px 10px 65px -26px rgba(0,0,0,0.75);
 -moz-box-shadow: -2px 10px 65px -26px rgba(0,0,0,0.75);
 box-shadow: -2px 10px 65px -26px rgba(0,0,0,0.75);
 margin-top: 33vh;
}
h1{
    text-align: center;
}
.userNav>h1{
    font-size: 5vh;
    line-height: 5vh;
    
}

input{
    padding: 7px;
    margin: 7px;
    width: 90%;
}
button{
    width: 90%;
    margin: 7px;
    background-color: royalblue;
    color: white;
    border: none;
    padding: 10px;
}
.adminbutton{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.adminbutton>input,.adminbutton>p{
    margin: 0px;
}
.adminbutton>input{
    margin-right: 10px;
    width: 15px;
    height: 15px;
}
body{
    background-color: whitesmoke;
}
.userContentHolder{
    
    margin: auto;
       background-color: white;
    width: 90%;
    display: flex;
    height: 500px;
    margin-top: 5vh;
}
.uchLeft{
    width: 50%;
   display: flex;
    justify-content: center;
    background-image: url("https://192.168.1.69:8080/public/static/media/sky.jpg");
    background-size: cover;
    flex-direction: column;
    justify-content: flex-start;
    
}
.uchLeft>h1{
    color: white;
}
.uchRight{
    width: 50%;
   

}
.uchRightTop{
    height: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}
.onlineButton,.busyButton,.offlineButton{
    height: 26%;
}
.uchRightTop{
    background-color: #e3e3e3;
}
.onlineButton{
    background-color: #26bf26;
    
}
.busyButton{
    background-color: red;
   
}
.offlineButton{
    background-color: grey;
   
}
.uchRightBottom{
    display: flex;
    height: 20%;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 6px;
}
.activeButton{
    border: 4px solid blue;  
}
.wrongLogin{
    background-color: red;
    color: white;
    padding: 5px;
}
.Adminstatus{
    display: flex;
    flex-direction: row;
}
.Adminstatus>button{
    height: 8vh !important;
    font-size: 2vh !important;

}
.wrongholder{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.adminuseroverview{
    margin: 0;
    height: 67.5vh;
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 15px;
    min-height: min-content;
}
.adminuseroverview>h4{
    margin: 0;
    
}
.adminusermanage{
    min-height: 22.5vh;
    max-height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0px;
}
.adminusermanage>h4{
    margin: 0;
}

.adminoverviewScroll{

    margin-top: 1vh;
    padding: 15px;
    height: 85%;
    width: 90%;
    overflow: scroll;
    scrollbar-width: none;
}
.adminoverviewScroll::-webkit-scrollbar {
    display: none;
  }
 
  .adminoverviewScroll {
    -ms-overflow-style: none;
  }
.sumprop{
    display: flex;
    justify-content: center;
    align-items: center;
}
.sumpropElment{
    display: flex;
    margin-right: 15px;
}
.cash{
    color: green;
    margin-left: 15px;
}
.sumprop>*>*{
    font-size: 3vh;
}
.stateTitle{
    font-size: 2em !important;
}
@media only screen and (max-width: 1100px) {
    .formholder {
      width: 100%;
      height: 50vh;
      margin: 0;
      margin-top: 5vh;
    }
    .adminav{
        display: flex;
        flex-direction: column;
        height: max-content;
    }
    .sumprop{
        flex-direction: column;
    }
  }