}
.modal {
    font-size: 12px;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 25px;

  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
    padding: 10px 5px;
  }
  .modal > .actions {
    width: 100%;
    align-self: flex-end !important;
    padding: 10px 5px;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }
  .scroll{
      overflow: scroll;
      height: 100% ;

      -ms-overflow-style: none;
      scrollbar-width: none;
      }
      .scroll::-webkit-scrollbar {
        display: none;
      }
  .helper{
      padding: 10px;
      font-size: medium;
      background-color: green !important;
      color: white !important;
  }
  .userdesc{
      height: 75vh;
  }
  .userdesc>p{
text-align: justify;
  }
  .upicture{
      width: 100%;
  }
  .viewbow{
      width: 50%;
      display: flex;
      flex-direction: row;
      height: 90%;
  }
  @media only screen and (max-width: 740px) {
      
    .ppup-content{
        border-radius: 0px;
        width: 100vw !important;
        height: 100vh;
    }
    .button{
height: 62px;
align-self: flex-end;
margin: auto;
    }
    .reviewcard{
        margin: 5px;
        background: #f0f0f0;
        padding: 15px;
        border-radius: 7px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .reviewcard>*{
        margin: 0px;
    }
  .userdesc>img{
    width: 100%;
    border-radius: 15px;
}
  }