.Nav-main{
    display: flex;
    padding: 6px 30px 6px 30px;
    width: 100vw;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: white !important;
}
.Nav-logo{
    justify-self: flex-start;
    display: flex;
    flex: row;
    align-items: center;
    
    
}
.Nav-contect{
  
    margin-right: 20px;
}
.link{
    margin-left: 20px;
    font-size: 20px !important;
}
.logo{
    max-height: 5vh !important;
    margin: 15px;
}
@media only screen and (max-width: 750px) {
    .Nav-main{
        display: flex;
        padding: 6px 30px 6px 30px;
        width: 100vw;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }
    .Nav-logo{
      text-align: center;
        
    }
    .Nav-contect{
        display: flex;
        margin: auto;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        margin-right: 20px;
        width: 100%;
    }
    .link{
        text-align: center;
        margin: 15px;
        font-size: larger;
       
    }
  }
  