#process{
    display: flex;
    justify-content: center;
    align-items: space-between;
    flex-direction: row;
    padding: 15px;
    padding-bottom: 75px;
}

.step{
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.ico{
    background-color: #760d83;
    border-radius: 100vh;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15px;
}
.ico>img{
    width: 50%;
}
.step>h4,.step>p,.step>h6{
    margin: 5px;
}
@media only screen and (max-width: 750px) {
    #process{
        flex-direction: column;
    }
    .step{
        margin-top: 50px;
    }
  }