
.user{
    display: flex;
    background-color: white;
margin-bottom: 2vh;
flex-wrap: wrap;
align-items: center;
justify-content: space-around;


}
.money{
    display: flex;

}
.user>*{
    margin: 5px;
}
.huf{
    margin-left: 5px;
    color: green;
}
.separator{
    background-color: black;
    height: 2rem;
    width: 0.1rem;
}
.messagButtonIamge{
    background-color: transparent;
    height: 100%;
}
.messageButton{
    height: 2.5rem;
    width:2.5rem;
    background-color: white;
    transition: 0.25s;
}
.messageButton:hover{
   background-color: lightgray;
}

.usermain{
    width: 90%;
    display: flex;
    background-color: white;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}
.messagedialog>*{
    width: 100%;
}
.messagedialog{
    
    width: 90%;
    display: none;
    flex-direction: column  ;
}

@media only screen and (max-width: 600px) {
   .user{
       flex-direction: column;
   }
   .separator{
       height: 1px;
       width: 90%;
   }
   .messageButton{
    width: 90%;
    }
    .messagButtonIamge{
        height: 100%;
        
    }    

  }